import { useState, useEffect } from 'react'
import { fetchCategories, deleteCategory } from '../http/catalogAPI.js'
import { Button, Container, Spinner, Table } from 'react-bootstrap'
import CreateCategory from '../components/CreateCategory.js'
import UpdateCategory from '../components/UpdateCategory.js'

const AdminCategories = () => {
    const [categories, setCategories] = useState(null) //   
    const [fetching, setFetching] = useState(true) //     
    const [createShow, setCreateShow] = useState(false) //    
    const [updateShow, setUpdateShow] = useState(false) //   
    //     , ,    
    const [change, setChange] = useState(false)
    // id ,        <UpdateCategory id={} />
    const [category, setCategory] = useState(null)

    const handleUpdateClick = (id) => {
        setCategory(id)
        setUpdateShow(true)
    }

    const handleDeleteClick = (id) => {
        deleteCategory(id)
            .then(
                data => {
                    setChange(!change)
                    alert(` ${data.name} `)
                }
            )
            .catch(
                error => alert(error.response.data.message)
            )
    }

    useEffect(() => {
        fetchCategories()
            .then(
                data => setCategories(data)
            )
            .finally(
                () => setFetching(false)
            )
    }, [change])

    if (fetching) {
        return <Spinner animation="border" />
    }

    return (
        <Container>
            <h1></h1>
            <Button onClick={() => setCreateShow(true)}> </Button>
            <CreateCategory show={createShow} setShow={setCreateShow} setChange={setChange} />
            <UpdateCategory id={category} show={updateShow} setShow={setUpdateShow} setChange={setChange} />
            {categories.length > 0 ? (
                <Table bordered hover size="sm" className="mt-3">
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {categories.map(item => 
                            <tr key={item.id}>
                                <td>{item.name}</td>
                                <td>
                                    <Button variant="success" size="sm" onClick={() => handleUpdateClick(item.id)}>
                                        
                                    </Button>
                                </td>
                                <td>
                                    <Button variant="danger" size="sm" onClick={() => handleDeleteClick(item.id)}>
                                        
                                    </Button>
                                </td>
                            </tr>
                        )}
                    </tbody>
                </Table>
            ) : (
                <p>  </p>
            )}
        </Container>
    )
}

export default AdminCategories